import React from 'react';
import { QueryClient, QueryClientProvider, useQuery } from '@tanstack/react-query';
import { fetchFloorPrice, fetchListings, fetchActivitesSales } from '../utils/api';
import Navbar from '../components/Navbar';

// Create a client
const queryClient = new QueryClient();

const CollectionPage = () => {
  const { data: floorPrice, isLoading: loadingFloorPrice } = useQuery({
    queryKey: ['floorPrice'],
    queryFn: fetchFloorPrice,
  });

  const { data: listings, isLoading: loadingListings } = useQuery({
    queryKey: ['listings'],
    queryFn: fetchListings,
  });

  const { data: sales, isLoading: loadingSales } = useQuery({
    queryKey: ['sales'],
    queryFn: fetchActivitesSales,
  });

  if (loadingFloorPrice || loadingListings || loadingSales) return <p>Loading...</p>;

  if (!floorPrice || !floorPrice.data) return <p>No floor price data available.</p>;
  if (!listings || !listings.data || listings.data.length === 0) return <p>No listings available.</p>;
  if (!sales || !sales.data || sales.data.length === 0) return <p>No sales data available.</p>;

  return (
    <>
      <Navbar />
      <div className="container mx-auto p-4">
        <h1 className="text-3xl font-bold mb-4">Meekolony Collection</h1>
        <p>Floor Price: {floorPrice.data.floorPrice} SOL</p>

        <h2 className="text-2xl mt-4">Recent Listings</h2>
        <div className="grid grid-cols-3 gap-4">
          {listings.data.map((listing) => (
            <div key={listing.id} className="p-4 border rounded">
              <img src={listing.image} alt={listing.name} />
              <p>{listing.name}</p>
              <p>{listing.price} SOL</p>
            </div>
          ))}
        </div>
        {console.log("sales-->", sales)}
        <h2 className="text-2xl mt-4">Recent Sales</h2>
        <ul>
          {sales.data.map((sale) => (
            <li key={sale.id}>{sale.name} sold for {sale.price} SOL</li>
          ))}
        </ul>
      </div>
    </>
  );
};

export default CollectionPage;
